// Table
// ––––––––––––––––––––––––––––––––––––––––––––––––––

table {
    border-spacing: 0;
    display: block;
    overflow-x: auto;
    text-align: left;
    width: 100%;
}

td,
th {
    border-bottom: 0.1rem solid var(--c-light-variant, #e1e1e1);
    padding: 1.2rem 1.5rem;

    &:first-child {
        padding-left: 0;
    }

    &:last-child {
        padding-right: 0;
    }
}

@media (min-width: 40rem) {
    table {
        display: table;
        overflow-x: initial;
    }
}
