*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: var(--font-base, 16px);
}

body {
  color: var(--c-text, #333);
  font-family: var(--font-family-regular);
  font-size: var(--font-base, 1.6em);
  font-weight: 400;
  letter-spacing: 0.01em;
  line-height: 1.6;
}

blockquote {
  border-left: 0.3rem solid var(--c-gray, #d1d1d1);
  margin-left: 0;
  margin-right: 0;
  padding: 1rem 1.5rem;
}
blockquote *:last-child {
  margin-bottom: 0;
}

.button,
button,
input[type=button],
input[type=reset],
input[type=submit] {
  background-color: var(--c-primary, #0080c0);
  border: 0.1rem solid var(--c-primary, #0080c0);
  border-radius: 0.4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 700;
  height: 3.8rem;
  letter-spacing: 0.1rem;
  line-height: 3.8rem;
  padding: 0 3rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}
.button:focus, .button:hover,
button:focus,
button:hover,
input[type=button]:focus,
input[type=button]:hover,
input[type=reset]:focus,
input[type=reset]:hover,
input[type=submit]:focus,
input[type=submit]:hover {
  background-color: var(--c-primary-variant, #01679b);
  border-color: var(--c-primary-variant, #01679b);
  color: #fff;
  outline: 0;
}
.button[disabled],
button[disabled],
input[type=button][disabled],
input[type=reset][disabled],
input[type=submit][disabled] {
  cursor: default;
  opacity: 0.5;
}
.button[disabled]:focus, .button[disabled]:hover,
button[disabled]:focus,
button[disabled]:hover,
input[type=button][disabled]:focus,
input[type=button][disabled]:hover,
input[type=reset][disabled]:focus,
input[type=reset][disabled]:hover,
input[type=submit][disabled]:focus,
input[type=submit][disabled]:hover {
  background-color: var(--c-gray, #666);
  border-color: var(--c-gray, #666);
}
.button.outline,
button.outline,
input[type=button].outline,
input[type=reset].outline,
input[type=submit].outline {
  background-color: transparent;
  color: var(--c-primary, #0080c0);
}
.button.outline:focus, .button.outline:hover,
button.outline:focus,
button.outline:hover,
input[type=button].outline:focus,
input[type=button].outline:hover,
input[type=reset].outline:focus,
input[type=reset].outline:hover,
input[type=submit].outline:focus,
input[type=submit].outline:hover {
  background-color: transparent;
  border-color: var(--c-primary-variant, #01679b);
  color: var(--c-primary-variant, #01679b);
}
.button.outline[disabled]:focus, .button.outline[disabled]:hover,
button.outline[disabled]:focus,
button.outline[disabled]:hover,
input[type=button].outline[disabled]:focus,
input[type=button].outline[disabled]:hover,
input[type=reset].outline[disabled]:focus,
input[type=reset].outline[disabled]:hover,
input[type=submit].outline[disabled]:focus,
input[type=submit].outline[disabled]:hover {
  border-color: inherit;
  color: var(--c-gray, #666);
}
.button.clear,
button.clear,
input[type=button].clear,
input[type=reset].clear,
input[type=submit].clear {
  background-color: transparent;
  border-color: transparent;
  color: var(--c-primary, #0080c0);
}
.button.clear:focus, .button.clear:hover,
button.clear:focus,
button.clear:hover,
input[type=button].clear:focus,
input[type=button].clear:hover,
input[type=reset].clear:focus,
input[type=reset].clear:hover,
input[type=submit].clear:focus,
input[type=submit].clear:hover {
  background-color: transparent;
  border-color: transparent;
  color: var(--c-primary-variant, #01679b);
}
.button.clear[disabled]:focus, .button.clear[disabled]:hover,
button.clear[disabled]:focus,
button.clear[disabled]:hover,
input[type=button].clear[disabled]:focus,
input[type=button].clear[disabled]:hover,
input[type=reset].clear[disabled]:focus,
input[type=reset].clear[disabled]:hover,
input[type=submit].clear[disabled]:focus,
input[type=submit].clear[disabled]:hover {
  color: var(--c-gray, #666);
}

code {
  background: var(--c-light, #efefef);
  border-radius: 0.4rem;
  font-size: 86%;
  margin: 0 0.2rem;
  padding: 0.2rem 0.5rem;
  white-space: nowrap;
}

pre {
  background: var(--c-light, #efefef);
  border-left: 0.3rem solid var(--c-primary, #0080c0);
  overflow-y: hidden;
}
pre > code {
  border-radius: 0;
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre;
}

hr {
  border: 0;
  border-top: 0.1rem solid var(--c-light, #efefef);
  margin: 3rem 0;
}

input[type=color],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=email],
input[type=month],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=text],
input[type=url],
input[type=week],
input:not([type]),
textarea,
select {
  -webkit-appearance: none;
  background-color: transparent;
  border: 0.1rem solid var(--c-gray, #d1d1d1);
  border-radius: 0.4rem;
  box-shadow: none;
  box-sizing: inherit;
  height: 3.8rem;
  padding: 0.6rem 1rem 0.7rem;
  width: 100%;
}
input[type=color]:focus,
input[type=date]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=email]:focus,
input[type=month]:focus,
input[type=number]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=tel]:focus,
input[type=text]:focus,
input[type=url]:focus,
input[type=week]:focus,
input:not([type]):focus,
textarea:focus,
select:focus {
  border-color: var(--c-primary, #0080c0);
  outline: 0;
}

select {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 30 8" width="30"><path fill="%23ar(--c-gray, #d1d1d1)" d="M0,0l6,8l6-8"/></svg>') center right no-repeat;
  padding-right: 3rem;
}
select:focus {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 30 8" width="30"><path fill="%23ar(--c-primary, #0080c0)" d="M0,0l6,8l6-8"/></svg>');
}
select[multiple] {
  background: none;
  height: auto;
}

textarea {
  min-height: 6.5rem;
}

label,
legend {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

fieldset {
  border-width: 0;
  padding: 0;
}

input[type=checkbox],
input[type=radio] {
  display: inline;
}

.label-inline {
  display: inline-block;
  font-weight: normal;
  margin-left: 0.5rem;
}

.container {
  margin: 0 auto;
  max-width: 112rem;
  padding: 0 2rem;
  position: relative;
  width: 100%;
}

.row {
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 100%;
}
.row.row-no-padding {
  padding: 0;
}
.row.row-no-padding > .column {
  padding: 0;
}
.row.row-wrap {
  flex-wrap: wrap;
}
.row.row-top {
  align-items: flex-start;
}
.row.row-bottom {
  align-items: flex-end;
}
.row.row-center {
  align-items: center;
}
.row.row-stretch {
  align-items: stretch;
}
.row.row-baseline {
  align-items: baseline;
}
.row .column {
  display: block;
  flex: 1 1 auto;
  margin-left: 0;
  max-width: 100%;
  width: 100%;
}
.row .column.column-offset-10 {
  margin-left: 10%;
}
.row .column.column-offset-20 {
  margin-left: 20%;
}
.row .column.column-offset-25 {
  margin-left: 25%;
}
.row .column.column-offset-33, .row .column.column-offset-34 {
  margin-left: 33.3333%;
}
.row .column.column-offset-40 {
  margin-left: 40%;
}
.row .column.column-offset-50 {
  margin-left: 50%;
}
.row .column.column-offset-60 {
  margin-left: 60%;
}
.row .column.column-offset-66, .row .column.column-offset-67 {
  margin-left: 66.6666%;
}
.row .column.column-offset-75 {
  margin-left: 75%;
}
.row .column.column-offset-80 {
  margin-left: 80%;
}
.row .column.column-offset-90 {
  margin-left: 90%;
}
.row .column.column-10 {
  flex: 0 0 10%;
  max-width: 10%;
}
.row .column.column-20 {
  flex: 0 0 20%;
  max-width: 20%;
}
.row .column.column-25 {
  flex: 0 0 25%;
  max-width: 25%;
}
.row .column.column-33, .row .column.column-34 {
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
}
.row .column.column-40 {
  flex: 0 0 40%;
  max-width: 40%;
}
.row .column.column-50 {
  flex: 0 0 50%;
  max-width: 50%;
}
.row .column.column-60 {
  flex: 0 0 60%;
  max-width: 60%;
}
.row .column.column-66, .row .column.column-67 {
  flex: 0 0 66.6666%;
  max-width: 66.6666%;
}
.row .column.column-75 {
  flex: 0 0 75%;
  max-width: 75%;
}
.row .column.column-80 {
  flex: 0 0 80%;
  max-width: 80%;
}
.row .column.column-90 {
  flex: 0 0 90%;
  max-width: 90%;
}
.row .column .column-top {
  align-self: flex-start;
}
.row .column .column-bottom {
  align-self: flex-end;
}
.row .column .column-center {
  align-self: center;
}

@media (min-width: 40rem) {
  .row {
    flex-direction: row;
    margin-left: -1rem;
    width: calc(100% + 2rem);
  }
  .row .column {
    margin-bottom: inherit;
    padding: 0 1rem;
  }
}
a {
  color: var(--c-primary, #0080c0);
  text-decoration: none;
}
a:focus, a:hover {
  color: var(--c-primary-variant, #01679b);
}

dl,
ol,
ul {
  list-style: none;
  margin-top: 0;
  padding-left: 0;
}
dl dl,
dl ol,
dl ul,
ol dl,
ol ol,
ol ul,
ul dl,
ul ol,
ul ul {
  font-size: 90%;
  margin: 1.5rem 0 1.5rem 3rem;
}

ol {
  list-style: decimal inside;
}

ul {
  list-style: circle inside;
}

.button,
button,
dd,
dt,
li {
  margin-bottom: var(--spacer-m, 1.25rem);
}

fieldset,
input,
select,
textarea {
  margin-bottom: var(--spacer-base, 1.5rem);
}

blockquote,
dl,
figure,
form,
ol,
p,
pre,
table,
ul {
  margin-bottom: var(--spacer-xl, 2.5rem);
}

table {
  border-spacing: 0;
  display: block;
  overflow-x: auto;
  text-align: left;
  width: 100%;
}

td,
th {
  border-bottom: 0.1rem solid var(--c-light-variant, #e1e1e1);
  padding: 1.2rem 1.5rem;
}
td:first-child,
th:first-child {
  padding-left: 0;
}
td:last-child,
th:last-child {
  padding-right: 0;
}

@media (min-width: 40rem) {
  table {
    display: table;
    overflow-x: initial;
  }
}
b,
strong {
  font-weight: bold;
}

p {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-heading, 700);
  letter-spacing: -0.1rem;
  margin-bottom: var(--spacer-lg, 2rem);
  margin-top: 0;
}

h1 {
  font-size: var(--h1-font-size, 4.6rem);
  line-height: 1.2;
}

h2 {
  font-size: var(--h2-font-size, 3.6rem);
  line-height: 1.25;
}

h3 {
  font-size: var(--h3-font-size, 2.8rem);
  line-height: 1.3;
}

h4 {
  font-size: var(--h4-font-size, 2.2rem);
  letter-spacing: -0.08rem;
  line-height: 1.35;
}

h5 {
  font-size: var(--h5-font-size, 1.8rem);
  letter-spacing: -0.05rem;
  line-height: 1.5;
}

h6 {
  font-size: var(--h6-font-size, 1.6rem);
  letter-spacing: 0;
  line-height: 1.4;
}

img {
  max-width: 100%;
}

.clearfix::after {
  clear: both;
  content: " ";
  display: table;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

body {
  margin: 0;
  min-height: 100vh;
  padding: 0;
  width: 100%;
}

*,
::after,
::before {
  box-sizing: border-box;
}

p:last-child {
  margin-bottom: 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
}
.grid.grid--has-rows.grid-columns--2 {
  grid-template-columns: repeat(2, minmax(320px, 1fr));
}
.grid.grid--has-rows.grid-columns--3 {
  grid-template-columns: repeat(3, minmax(320px, 1fr));
}
.grid.grid--has-rows.grid-columns--4 {
  grid-template-columns: repeat(4, minmax(320px, 1fr));
}
.grid.grid--has-rows.grid-columns--5 {
  grid-template-columns: repeat(5, minmax(320px, 1fr));
}
.grid.grid--has-rows.grid-columns--6 {
  grid-template-columns: repeat(6, minmax(320px, 1fr));
}

.image-element__wrap img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.shopify-section {
  width: 100%;
}
.shopify-section header,
.shopify-section footer,
.shopify-section section,
.shopify-section .section {
  margin: 0 auto;
  max-width: var(--site-width);
  width: 100%;
}
.shopify-section .is--full-width {
  max-width: none;
  width: 100%;
}

.section__collection-list .collection-list__item .item__wrap {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.section__collection-list .collection-list__item .item__thumbnail {
  flex: 1;
}
.section__collection-list .collection-list__item .item__thumbnail .image-element__wrap {
  height: 100%;
}

/**
  * Custom SCSS
 **/
@font-face {
  font-family: "Nuacht Sans";
  src: url("NuachtSans-RegularWEBX.woff") format("woff"), url("NuachtSans-RegularWEBX.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Nuacht Serif Headline";
  src: url("NuachtSerifHeadline-BoldWEBX.woff") format("woff"), url("NuachtSerifHeadline-BoldWEBX.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}